import { Identified } from "@moviemasher/moviemasher.js"



export enum ActivityType {
  Analyze = 'analyze',
  Complete = 'complete',
  Error = "error",
  Load = 'load',
  Render = 'render',
}

export interface ActivityInfo extends Identified {
  type: ActivityType
  label?: string
  steps?: number
  step?: number
  error?: string
  value?: string
}
