const colorRgbToHex = (rgb) => {
    let r = rgb.r.toString(16);
    let g = rgb.g.toString(16);
    let b = rgb.b.toString(16);
    if (r.length < 2)
        r = `0${r}`;
    if (g.length < 2)
        g = `0${g}`;
    if (b.length < 2)
        b = `0${b}`;
    return `#${r}${g}${b}`;
};
